'use client';

import React, { useState, useEffect } from 'react';
import { useRouter, useSearchParams } from 'next/navigation'; // To handle URL params

const ShopPage = () => {
  const router = useRouter();
  const searchParams = useSearchParams();
  const category = searchParams.get('category') || 'All'; // Get category from URL or default to 'All'
  
  const [products, setProducts] = useState([]);
  
  useEffect(() => {
    // Fetch products based on category
    const fetchProducts = async () => {
      const allProducts = await fetch('/api/products'); // Replace with actual API call if needed
      const data = await allProducts.json();
      
      if (category === 'All') {
        setProducts(data);
      } else {
        setProducts(data.filter((product) => product.category === category));
      }
    };
    
    fetchProducts();
  }, [category]);

  return (
    <section className="max-w-screen-xl mx-auto py-8">
      <h2 className="text-center text-gray-800 text-2xl font-bold uppercase mb-6">
        {category} Products
      </h2>

      {/* Product Grid */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
        {products.map((product, index) => (
          <div key={index} className="bg-white shadow-lg rounded border border-gray-300 p-4">
            {/* Product Image */}
            <div className="w-full h-64 rounded-md overflow-hidden flex items-center justify-center">
              <img
                src={product.image}
                alt={product.title}
                className="w-full h-full object-cover"
              />
            </div>
            {/* Product Info */}
            <h3 className="text-sm font-bold mt-4">{product.title}</h3>
            <p className="text-xs text-gray-500">{product.category}</p>
            <p className="text-sm font-bold mt-2">
              <span className="line-through text-gray-400">{product.oldPrice}</span>{' '}
              <span className="text-green-500">{product.price}</span>
            </p>
          </div>
        ))}
      </div>
    </section>
  );
};

export default ShopPage;
